export type FileInfoType = 'd' | '-' | 'l';

export enum FileInfoEnum {
  DIRECTORY = 'DIRECTORY',
  FILE = 'FILE',
}

export interface FileInfo {
  type: FileInfoEnum;
  name: string;
  size: number;
  modifyTime: Date;
}

export interface ListMetaData {
  name: string;
  type: 'D' | 'A';
  size: number;
  modifyTime: Date;
}
